export default {
    state:{//state
        show:false
    },
    mutations:{
        switch_dialog(state){//statestate
            state.show = state.show?false:true;
            //state
        }
    },
    actions:{
        switch_dialog({commit},payload){
            commit({
                type:'switch_dialog',
            })
        }
    }
}


